

html {
  overflow-y: scroll;
}

body {
background-color: #4A7AD3;
-ms-flex-pack: center;
    justify-content: center;
}

.App {
  text-align: center;
}

.header {
  background-color: #3D2B56;
  height: 120px;
  padding: 20px 40px;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);

}

.priority {
  background: #B23027;
}

.todo-app {
  max-width: 90%;
  width: 800px;
  margin: 40px auto;  
  display:flex-inline;
  -ms-flex-pack: center;
      justify-content: center  
}

h1 {
  text-align:center;
  color:#ffffff;
  text-shadow: 0px 3px 15px rgba(0,0,0,.5);

}

.text-box {
  width:100%;
  background-color: orange;
  display: block;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;


}

.text-box form {
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  height: 30px;
  background-color:pink;
}
input[type=text] {
  width: 90%;
  border: 1px; 
  border-radius: 0;
  padding: 6px;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
  -webkit-appearance: none;
  display: inline-block;
  -ms-flex-positive: 5;
      flex-grow: 5;
}

input[type=submit] {
  width: auto;
  border:none;
  background-color:#599bb3;
  display:inline-block;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
  cursor:pointer;
  color:#ffffff;  
  padding: 6px 12px;
}

ul {
  display: flex-inline;
  color: white;1
  justify-content: center;
  padding:10px 0px; 
}

li {
  display:-ms-flexbox;
  display:flex;
  margin: 5px;
  background-color: navy;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
}

span {
  display: inline-block;
  -ms-flex: 6 1;
      flex: 6 1;
  margin: auto 10px;
}

button {
  max-width:50px;
  border:none;
  background-color:#599bb3;
  display:inline-block;
  -webkit-box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
          box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
  cursor:pointer;
  color:#ffffff;  
  padding:4px 16px;
  margin: 5px;
  -ms-flex:1 1;
      flex:1 1;
}

button:hover {
  background-color: #FFA500;
}

.checked {
  text-decoration:line-through;
  background-color:grey;
  color:darkgrey;
}

@media only screen and (max-width: 600px) {
    body {
        background-color: lightblue;
    }

    button {
      max-width:25px;
      padding: 1px 1px;
    }
}


